.consultation-container {
    margin: 1.5rem;
    width: 100%;
}

.consultation-container .title {
    font-size: 1.3rem;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    margin-bottom: 1rem;
}

.consultation-container .title p {
    margin-bottom: 0px;
}

.consultation-container .columns {
    display: flex;
}

.consultation-container .column.main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}

.consultation-container .column.detail {
    margin: 0rem 3rem;
}

.consultation-container .actions {
    margin: 2rem 0;
}

.consultation-container .actions Button {
    margin: 0rem .5rem;
}

.consultation-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.consultation-container .detail ul li {
    margin: 1rem .5rem;
    color: rgba(0, 0, 0, 0.548);
    font-size: 1.2rem;
}